<template>
  <div class="v-container">
    <div class="col-8 center-container">
      <div class="row search-form">
        <h2>Настройки поиска</h2>
        <div class="input-field">
          <label for="">Поиск</label>
          <v-input default>
            <v-text-field
              type="text"
              v-model="searchText"
              placeholder="Введите ключевые слова"
            />
          </v-input>
        </div>
        <div class="input-field">
          <label for="color">Цвет</label>
          <v-select name="color" id="" v-model="chosenColor" :items="colors">
          </v-select>
        </div>
        <div class="input-field">
          <label for="">Тема поиска</label>
          <v-select
            name="category"
            id=""
            v-model="chosenCategory"
            :items="categories"
          >
          </v-select>
        </div>
        <div class="input-field">
          <label for="">Язык поиска</label>
          <v-select name="lang" id="" v-model="chosenLang" :items="langs">
          </v-select>
        </div>
        <div class="button-wrapper">
          <v-btn class="search-photos" @click="searchPhotos">Поиск</v-btn>
        </div>
      </div>
      <div class="row">
        <div class="photos col">
          <div
            class="col preview-photo"
            v-for="(img, index) in photolist"
            :key="index"
            @click="getFullViewURL"
            :data-fullurl="img.largeImageURL"
          >
            <img :src="img.previewURL" alt="" />
            <h5 class="col">Автор: {{ img.user }}</h5>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="pagination">
          <ul
            class="col flex flex-wrap"
            @click.prevent="
              getPage();
              searchPhotos();
            "
          >
            <li data-indexpage="1">1</li>
            <li data-indexpage="2">2</li>
            <li data-indexpage="3">3</li>
            <li data-indexpage="4">4</li>
            <li data-indexpage="5">5</li>
            <li data-indexpage="6">6</li>
            <li data-indexpage="7">7</li>
            <li data-indexpage="8">8</li>
            <li data-indexpage="9">9</li>
            <li data-indexpage="10">10</li>
          </ul>
        </div>
      </div>
      <div class="full-view-background" v-if="fullViewURL">
        <div class="full-view">
          <div class="img-wrapper">
            <img :src="fullViewURL" alt="" />
          </div>
          <div class="commentary">
            <div class="commentaries-field">
              <div
                class="comment"
                v-for="(comment, index) in comments"
                :key="index"
              >
                <h5>{{ comment.author }}</h5>
                <p>{{ comment.comment }}</p>
              </div>
            </div>
            <v-input class="add-comments">
              <v-text-field
                placeholder="Ваше имя"
                v-model="author"
              ></v-text-field>
              <v-text-field
                placeholder="Оставьте ваш комментарий"
                v-model="comment"
              ></v-text-field>
            </v-input>
            <v-btn @click="addComment">Оставить комментарий</v-btn>
            <v-icon class="close-button" @click="fullViewURL = false"
              >close</v-icon
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      proxy: "https://cors-anywhere.herokuapp.com/",
      apiKey: "?key=18203385-ff86ede1091ec281df5741b1d",
      photolist: null,
      searchText: null,
      chosenCategory: null,
      chosenLang: null,
      chosenColor: null,
      chosenPage: "10",
      categories: [
        "backgrounds",
        "fashion",
        "nature",
        "science",
        "education",
        "feelings",
        "health",
        "people",
        "religion",
        "places",
        "animals",
        "industry",
        "computer",
        "food",
        "sports",
        "transportation",
        "travel",
        "buildings",
        "business",
        "music",
      ],
      colors: [
        "grayscale",
        "transparent",
        "red",
        "orange",
        "yellow",
        "green",
        "turquoise",
        "blue",
        "lilac",
        "pink",
        "white",
        "gray",
        "black",
        "brown",
      ],
      langs: ["en", "ru"],
      fullViewURL: null,
      comments: [{ author: "Bill Kosby", comment: "nice Photo!" }],
      author: null,
      comment: null,
    };
  },
  mounted() {
    this.searchDefaultPhotos();
  },
  computed: {
    querryText() {
      if (this.searchText === null) return "";
      return this.searchText.split(" ").join("+");
    },
    querryCategory() {
      if (this.chosenCategory === null) return "";
      return "&category=" + this.chosenCategory;
    },
    querryColor() {
      if (this.chosenColor === null) return "";
      return "&color=" + this.chosenColor;
    },
    querryLang() {
      if (this.chosenLang === null) return "";
      return "&lang=" + this.chosenLang;
    },
  },
  methods: {
    addComment() {
      if (!this.comment || !this.author) return;
      let obj = { author: this.author, comment: this.comment };
      this.comments.push(obj);
    },
    getFullViewURL() {
      if (!event.target.closest(".preview-photo")) return;
      this.fullViewURL = event.target.closest(".preview-photo").dataset.fullurl;
    },
    searchDefaultPhotos() {
      axios
        .get(
          `${this.proxy}https://pixabay.com/api/${this.apiKey}&q=popular&page=${this.chosenPage}`
        )
        .then((response) => {
          this.photolist = response.data.hits;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getPage() {
      if (!event.target.closest("li")) return;
      this.chosenPage = event.target.dataset.indexpage;
    },
    searchPhotos() {
      axios
        .get(
          `${this.proxy}https://pixabay.com/api/${this.apiKey}&q=${this.querryText}${this.querryCategory}${this.querryColor}${this.querryLang}&page=${this.chosenPage}`
        )
        .then((response) => {
          this.photolist = response.data.hits;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getPhotoList() {
      axios
        .get(`https://pixabay.com/api/${this.apikey}&q${this.querryText}`)
        .then((response) => {
          this.photolist = response;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

<style lang="scss">
h2 {
  width: 100%;
  margin: 1em;
}
.center-container {
  margin: auto;
}
.search-form {
  display: flex;
  justify-content: center;
  box-shadow: -5px -5px 2px rgba(255, 255, 255, 0.719),
    0px 0px 5px rgba(0, 0, 0, 0.3), 15px 15px 15px rgba(0, 0, 0, 0.3);
  padding: 1em;
}
.photos {
  display: flex;
  flex-wrap: wrap;

  margin: 2em 0;
  box-shadow: -5px -5px 2px rgba(255, 255, 255, 0.719),
    0px 0px 5px rgba(0, 0, 0, 0.3), 15px 15px 15px rgba(0, 0, 0, 0.3);
}

.preview-photo {
  border: 1px solid rgba(0, 0, 0, 0.24);
  margin: 1em;
  display: flex;
  flex-wrap: wrap;
  box-shadow: -3px -3px 2px rgba(255, 255, 255, 0.3),
    5px 5px 5px rgba(0, 0, 0, 0.3), 5px 5px 5px rgba(0, 0, 0, 0.3);
  align-items: center;
  justify-content: center;
}
.input-field {
  margin: 1em;
}
.preview-photo img {
  object-fit: contain;
}
.preview-photo h5 {
  width: 100%;
}
.pagination {
  display: flex;
  justify-content: center;
  width: 100%;
}
.pagination ul {
  display: flex;
  list-style: none;
  justify-content: center;
}
.pagination ul li {
  cursor: pointer;
}
.pagination ul li:after {
  content: "\00A0";
}
.button-wrapper {
  justify-content: center;
  width: 100%;
  display: flex;
  align-self: center;
}
.full-view {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  width: 60vw;
  min-height: 60vh;
  background: rgb(255, 255, 255);
  padding: 2em;
}
.img-wrapper {
  width: 80%;
  object-fit: contain;
}
.img-wrapper img {
  width: 100%;

  object-fit: contain;
}
.commentary {
  width: 100%;
}
.close-button {
  position: absolute !important;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
.full-view-background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(128, 128, 128, 0.616);
  display: flex;
  justify-content: center;
  align-items: center;
}
.comment {
  display: flex;
  justify-content: center;
  align-items: center;
  h5 {
    width: 20%;
    text-align: right;
  }
  p {
    text-align: left;
    width: 50%;
    margin: 0;
    margin-left: 2em;
    padding-left: 1em;
    border-left: 1px solid rgba(0, 0, 0, 0.178);
    border-bottom: 1px solid rgba(0, 0, 0, 0.178);
  }
}
.add-comments {
  display: flex;
  justify-content: center;
  width: 80%;
  margin: 1em auto;
  border-top: 2px solid rgba(0, 0, 0, 0.178);
}
</style>
